<template> 
<div class="height_100p">   
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt -->
  <div class="leftvideo-page-body">
   <transition name="toRight">
    <div class="right hover-effect" v-show="dataArr.leftObj[0].showNow">
        <iframe class="yk" :src="videoSrc" frameborder=0 allowfullscreen></iframe>
    </div>
   </transition>
   <div class="left">
      <div class="item hover-effect click-effect" v-for="(item, index) in dataArr.leftObj">
        <transition name="toTop">
          <div v-show="item.showNow">
            <div class="item-left">
              <img :src="item.titleImg">
            </div>
            <div class="item-right">
              <div class="title title-fs">{{item.title}}</div>
              <div class="content normal-txt-f">{{item.content}}</div>
            </div>
          </div>
        </transition>
      </div>
   </div>
  </div>
</div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  @import "../../../static/css/leftVideo.css";
  
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //
        titleMsg:{
          left:'',
          right:'',
        },
        dataArr:{
          leftObj:[
            {
              title:'',
              content:'',
              titleImg:'static/img/videotest.jpg',
              showNow:false
            },
            {
              title:'',
              content:'',
              titleImg:'static/img/videotest.jpg',
              showNow:false
            },
            {
              title:'',
              content:'',
              titleImg:'static/img/videotest.jpg',
              showNow:false
            },
          ],
        },
        videoSrc:'',//
        description:'',
        vid:'XMzQzNDQ0MzcxNg'
      }
    },
    methods:{
    },
    mounted(){
      //itemtm
      var obj = this.dataArr.leftObj;
      obj[0].showNow = true;
      setTimeout(function(){
        obj[1].showNow = true;
      },200)
      setTimeout(function(){
        obj[2].showNow = true;
      },300)
      this.videoSrc = 'http://player.youku.com/embed/' + this.vid + '==?client_id=78b2cfcefe58c217';
    }
  }
</script>


