<template> 
<div class="height_100p">   
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt -->
  <div class="texttwo-page-body">
    <transition name="toBottom">
      <div class="content one" v-show="show">
        <div class="liubianxing hover-effect">
          <p>{{blue.title1}}</p>
          <p>{{blue.title2}}</p>
        </div>
        <div class="txt normal-txt-fs">
          {{blue.content}}
        </div>
      </div>
    </transition>
    <transition name="toRight">
      <div class="content two" v-show="show">
        <div class="liubianxing hover-effect">
          <p>{{orange.title1}}</p>
          <p>{{orange.title2}}</p>
        </div>
        <div class="txt normal-txt-fs">
          {{orange.content}}
        </div>
      </div>
    </transition>
    <transition name="toLeft">
      <div class="content three" v-show="show">
        <div class="liubianxing hover-effect">
          <p>{{green.title1}}</p>
          <p>{{green.title2}}</p>
        </div>
        <div class="txt normal-txt-fs">
          {{green.content}}
        </div>
      </div>
    </transition>
  </div>
</div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  @import "../../../static/css/textTwo.css";
  
</style>
<script>
  import pptHead from './header.vue'
  import $ from 'jquery'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //
        titleMsg:{
          left:'',
          right:'',
        },
        show:false,
        // 
        blue:{
          title1:'',
          title2:'',
          content:''
        },
        // 
        orange:{
          title1:'',
          title2:'',
          content:''
        },
        // 
        green:{
          title1:'',
          title2:'',
          content:''
        },
      }
    },
    mounted(){
      this.show = true
    }
  }
</script>


