<template>
 <!--  tmd -->
  <div class="home-page-body" @click="showPPT">
    <!--  -->
    <transition name="toBottom"> 
      <div class="top-bg" v-show="show">
        <img src="static/img/home-top-left.png">
      </div>
    </transition>
    <!--  -->
    <transition name="toTop"> 
      <div class="bottom-bg" v-show="show">
        <img src="static/img/home-right-bottom.png">
      </div>
    </transition>
    <transition name="transparentAppear"> 
      <div class="height_100p" v-show="show">
        <!-- md12345 -->
        <div class="title-index">
          <span class="num">{{pageIndex}}</span>
          <span class="english">{{pageIndexEN}}</span>
        </div>
        <!--  -->
        <div class="txt-box">
          <img class="left-pic" src="static/img/txt.png" alt="" srcset="">
          <span class="txt" style="color: #319095;">{{pageName}}</span>
          <span class="txt" style="color: #826c4a;">{{pageName1}}</span>
          <img class="right-pic" src="static/img/txt1.png" alt="" srcset="">
        </div>
        <div class="subTxt">{{subName}}</div>
      </div>
    </transition>
  </div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  @import "../../../static/css/home.css";
</style>
<script>
  export default{
    components: {
    },
    data(){
      return{
        //
        'show':false,
        //
        'pageIndex':' ',
        'pageIndexEN':' ',
        'pageName':'',
        'pageName1':'',
        'subName':''
      }
    },
    methods:{
      showPPT:function(){
        this.show = true;
      }
    },
    mounted(){
      $('.page-head').css({'display':'none'});
      this.show = true;
    },
    destroyed(){
      $('.page-head').css({'display':'block'});
    }
  }
</script>


