<template> 
<div class="height_100p">   
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt -->
  <div class="txtapf-page-body">
    <div class="item-box hover-effect" v-for="(item, index) in dataArr">
      <transition name="toTop">
        <div class="height_100p" v-show="show">
          <div class="left-txt">
            <div class="title title-fs">
              {{item.title}}
            </div>
            <div class="normal-txt-fs">{{item.content}}</div>
          </div>
          <div class="right-pic">
            <img :src="item.imgSrc">
          </div>
        </div>
      </transition>
    </div>
  </div>
</div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  @import "../../../static/css/txtAndPicFourth.css";
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //
        titleMsg:{
          left:'',
          right:'',
        },
        dataArr:[
          {
            title:'',
            content:'',
            imgSrc:'static/img/zuo55555.jpg',
          },
          {
            title:'',
            content:'',
            imgSrc:'static/img/zuo5.jpg',
          },
          {
            title:'',
            content:'',
            imgSrc:'static/img/zuo55.jpg',
          },
          {
            title:'',
            content:'',
            imgSrc:'static/img/zuo555555.jpg',
          },
        ],
        show:false,
      }
    },
    methods:{
    },
    mounted(){
      this.show = true;
    }
  }
</script>


