<template>
 <div class="height_100p">
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt -->
  <div class="textone-page-body">
    <audio id="bg-music" :src="txtObj.bgMusic"></audio>
    <transition name="toTop">
      <div v-show="show">
        <div class="t-c title-fs">
          {{txtObj.title}}
        </div>
        <div class="text-content normal-txt-fs"><br>
          <div class="item" v-for="(item, index) in txtObj.content">{{item}}</div>
        </div>
      </div>
    </transition>
  </div>
 </div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  .textone-page-body{
    width: 100%;
    height: calc(~'100% - 100px');
    padding: 6% 220px 100px;
    overflow-y: auto;
    .music-play{
      position: absolute;
      cursor: pointer;
      top:20%;
      right: 5%;
      width: 78px;
      height: 78px;
      border-radius: 50%;
      background-color: #f5841c;
      box-shadow: 0px 0px 12px 0px 
        rgba(245, 132, 28, 0.22);
      border: solid 1px #f1821c;
      text-align: center;
      img{
        margin-top: 20%;
        width: 40px;
        height: 40px;
      }
    }
    .text-content{
      .item{
        line-height: 2.5em;
        &:nth-child(2n){
          font-weight: bold;
        }
      }
    }
  }
</style>
<script>
  import $ from 'jquery'
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //
        titleMsg:{
          left:'',
          right:'',
        },
        rotateAngle:'rotate(0deg)',//
        time: '20000000000000s',
        musicActive:false,
        show:false,
        txtObj:{
          title:'',
          content:[
            '1 ',
            ' A    B    C     D     E',
            '2 ',
            'A        B        C',
            '3',
            'A      B     C        D',
            '4',
            'A          B          C     D.',
            '5',
            'A    B      C',
            '6',
            'A   B  C   D.',
            '7',
            'A     B    C     D  E.',
            '8',
            'A     B    C      D    E',
            '9',
            'A       B        C         D',
            '10',
            'A       B      C      D        E.',
            '11',
            'A    B    C    D ',
            '12',
            'A   B35   C13  D.',
            '13',
            'A    B    C',
           
          ],
          bgMusic:'static/sound/bg.mp3'
        }
      }
    },
    methods:{
      playMusic:function(){
        var music = $('#bg-music')[0];
        if(this.musicActive){
          this.rotateAngle = 'rotate(0deg)'
          this.musicActive = false;
          this.time = '0s';
          music.pause();
        }else{
          music.play();
          this.time = '20000000000000s';
          this.rotateAngle = 'rotate(1000000000000000deg)';
          this.musicActive = true;
        }
      }
    },
    mounted(){
      this.show = true;
    }
    
  }
</script>


