<template>  
<div class="height_100p">      
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt -->
  <div class="lefttext-page-body" @click="orderEvent">
   <div class="left">
      <div class="item hover-effect click-effect" v-for="(item, index) in dataArr.leftObj">
        <transition name="toTop">
          <div v-show="item.showNow">
            <div class="item-left">
              <img :src="item.titleImg">
            </div>
            <div class="item-right">
              <div class="title title-fs">{{item.title}}</div>
              <div class="content normal-txt-f">{{item.content}}</div>
            </div>
          </div>
        </transition>
      </div>
   </div>
   <transition name="toLeft">
    <div class="right hover-effect" v-show="dataArr.leftObj[0].showNow">
        <img :src="dataArr.bigImg">
    </div>
   </transition>
  </div>
  </div>
</template>

<style lang="less">
@import "../../../static/css/common.less";
@import "../../../static/css/animate.css";
@import "../../../static/css/lefttext.css";
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //
        titleMsg:{
          left:'',
          right:'',
        },
      dataArr: {
        leftObj: [
          {
            title: "",
            content:
              "",
            titleImg: "static/img/videotest.jpg",
            showNow: false
          },
          {
            title: "",
            content:
              "",
            titleImg: "static/img/videotest.jpg",
            showNow: false
          },
          {
            title: "",
            content:
              "",
            titleImg: "static/img/videotest.jpg",
            showNow: false
          }
        ],
        bigImg: "static/img/videotest.jpg"
      }
    };
  },
  methods: {
    orderEvent: function() {
      this.orderDisplay(this.txtArr);
    }
  },
  mounted() {
    //itemtm
    var obj = this.dataArr.leftObj;
    obj[0].showNow = true;
    setTimeout(function() {
      obj[1].showNow = true;
    }, 200);
    setTimeout(function() {
      obj[2].showNow = true;
    }, 300);
  }
};
</script>


