<template>
 <div class="height_100p">
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt -->
  <div class="rightFourPic-page-body-one" @click="orderEvent">
   <div class="left">
      <div class="item hover-effect click-effect" v-for="(item, index) in dataArr.leftObj">
        <transition name="toTop">
          <div v-show="item.showNow">
            <div class="item-left">
              <img :src="item.titleImg">
            </div>
            <div class="item-right">
              <div class="title title-fs">{{item.title}}</div>
              <div class="content normal-txt-fs" v-for="(itemTxt, index) in item.content">
                <p>{{itemTxt}}</p>
              </div>
            </div>
          </div>
        </transition>
      </div>
   </div>
   <transition name="toLeft">
    <div class="right" v-show="dataArr.leftObj[0].showNow">
        <img class="hover-effect" v-for="(imdSrc, index) in dataArr.bigImg" :src="imdSrc">
    </div>
   </transition>
  </div>
 </div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  @import "../../../static/css/rightFourPic.css";

</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //
        titleMsg:{
          left:'',
          right:'',
        },
        dataArr:{
          leftObj:[
            {
              title:'',
              content:[
                '90100110120130140150',
                '',
                '',
                '20',
              ],
              titleImg:'static/img/videotest.jpg',
              showNow:false
            },
            // {
            //   title:'',
            //   content:[
            //     '',
            //   ],
            //   titleImg:'static/img/videotest.jpg',
            //   showNow:false
            // },
            // {
            //   title:'',
            //   content:[
            //     '',
            //   ],
            //   titleImg:'static/img/videotest.jpg',
            //   showNow:false
            // },
           ],
          //
          bigImg:[
            'static/img/tiyuke8.jpg',
            'static/img/tiyuke88.jpg',
            'static/img/tiyuke8888.jpg',
            'static/img/tiyuke888.jpg',
            // 'static/img/tiyuke11111.jpg',
            // 'static/img/tiyuke111111.jpg',
            ]
        }
      }
    },
    methods:{
      orderEvent: function(){
        this.orderDisplay(this.txtArr);
      }
    },
    mounted(){
      //itemtm
      var obj = this.dataArr.leftObj;
      obj[0].showNow = true;
      setTimeout(function(){
        obj[1].showNow = true;
      },200)
      setTimeout(function(){
        obj[2].showNow = true;
      },300)
    }
  }
</script>


