<template>
 <div class="height_100p">
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt -->
  <div class="leftxtmultpic-page-body" @click="orderEvent">
   <div class="left">
      <div class="item hover-effect click-effect" v-for="(item, index) in dataArr.leftObj">
        <transition name="toTop">
          <div v-show="item.showNow">
            <div class="item-left">
              <img :src="item.titleImg">
            </div>
            <div class="item-right">
              <div class="title title-fs">{{item.title}}</div>
              <div class="content normal-txt-fs" v-for="(itemTxt, index) in item.content">
                <p>{{itemTxt}}</p>
              </div>
            </div>
          </div>
        </transition>
      </div>
   </div>
   <transition name="toLeft">
    <div class="right" v-show="dataArr.leftObj[0].showNow">
        <img class="hover-effect" v-for="(imdSrc, index) in dataArr.bigImg" :src="imdSrc">
    </div>
   </transition>
  </div>
 </div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  .leftxtmultpic-page-body{
    width: 100%;
    height: calc(~'100% - 100px');
    padding: 6% 10% 0px 10%;
    box-sizing: border-box;
    .left{
      width: 55%;
      height: 80%;
      float: left;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .item{
        .item-left{
          float: left;
          width: 78px;
          height: 78px;
          img{
            width: 100%;
            height: 100%;
            border-radius: 50%;
            border: solid 1px #319095;
          }
        }
        .item-right{
          float: left;
          width: calc(~'100% - 110px');
          margin-left: 32px;
        }
      }
    }
    .right{
      width: 40%;
      height:100%;
      margin-left: 5%;
      margin-top: -5%;
      float: left;
      img{
        float: left;
        width: 50%;
        height: 100%/3;
        cursor: pointer;
        clip-path: inset(2% round 20% 0% 20% 0%);
      }
    }
  }
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //
        titleMsg:{
          left:'',
          right:'',
        },
        dataArr:{
          leftObj:[
            {
              title:'',
              content:[
                '1.',
                '2.',
                // '3.',
              ],
              titleImg:'static/img/videotest.jpg',
              showNow:false
            },
            {
              title:'',
              content:[
                '',
                // '',
              ],
              titleImg:'static/img/videotest.jpg',
              showNow:false
            },
            {
              title:'',
              content:[
                '',
                '',
                '',
                '',
              ],
              titleImg:'static/img/videotest.jpg',
              showNow:false
            },
          ],
          //
          bigImg:[
            'static/img/yangguang22.jpg',
            'static/img/yangguang2222.jpg',
            'static/img/yangguang222.jpg',
            'static/img/yangguang.png',
            'static/img/yangguang22222.jpg',
            'static/img/yangguang222222.jpg',
            ]
        }
      }
    },
    methods:{
      orderEvent: function(){
        this.orderDisplay(this.txtArr);
      }
    },
    mounted(){
      //itemtm
      var obj = this.dataArr.leftObj;
      obj[0].showNow = true;
      setTimeout(function(){
        obj[1].showNow = true;
      },200)
      setTimeout(function(){
        obj[2].showNow = true;
      },300)
    }
  }
</script>


